<template>
<div>

    <!-- @change="emit('updateIngredient', name, quantity, unit)" -->
  <b-input-group prepend="Ingredient" class="mb-2" @change="emit(updateIngredient, name, quantity, unit)">
    <input v-model="name">
    <input v-model="quantity" type="number" min="1" >
    <select v-model="unit" id="unit" type="text">
        <option value="" disabled selected>Unit</option>
        <option v-for="u in units" :key="u">{{ u }}</option>
    </select>
  </b-input-group>

  <b-icon-plus-circle></b-icon-plus-circle>

</div>
</template>

<script>

export default {
    name: "Ingredient",
    components:
    {
        BIconPlusCircle
    },
    data() {
    return {
    units: [],
    name: "",
    quantity: "",
    unit: "",
    };
  },
  mounted()
  {
    this.setUnit();
  },
  methods: {
    setUnit: async function()
      {
        try {
        const response = await this.axios.get(
          this.$root.store.server_domain + "/getUnits",        
        );
        
        this.units = response.data;
        
        
      } catch (error) {

        console.log(error);
      }
      },
  }

}
</script>

