<template>
  <div>
    <h1> {{ recipe.title }}</h1>
    <p>
        Ready in: {{ recipe.readyInMinutes }} <br>
        {{ recipe.popularity }} 
        <img src="https://cdn.shopify.com/s/files/1/0531/4392/9023/products/DesignohneTitel_15_1000x.png?v=1610292476" height="50" width=50>
         <br>
        <span v-if="recipe.vegan">
            <img src="https://upload.wikimedia.org/wikipedia/commons/5/5b/Vegan_friendly_icon.svg" height="50" width=50>
        </span>
        <span v-if="recipe.vegetarian">
            <img src="https://t3.ftcdn.net/jpg/05/33/16/84/360_F_533168476_ZwcHp3VXYOAr8RaAHxt2p1FwT0TbXTzj.jpg" height="50" width=50>
        </span>
        <span v-if="recipe.glutenFree">
            <img src="data:image/png;base64," height="50" width=50>
        </span>
        <span v-if="recipe.favorite">
            <img src="data:image/jpeg;base64,">
        </span>
        <span v-if="recipe.watched">
            <img src="data:image/png;base64,">
        </span>    

        <img :src="recipe.image">

    </p>
    
  </div>
</template>

<script>
export default {
    name: "RecipePreview",
    mounted() {
    this.axios.get(this.recipe.image).then((i) => {
      this.image_load = true;
    });
    },
    data() {
        return {
        image_load: false
        };
    },
    props: {
        recipe: {
        type: Object,
        required: true
        }
    }
};

</script>

